import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useMutation } from "react-query";
import { CButton, CCol, CContainer, CRow } from "@coreui/react";
import { AcknowledgeMail } from "src/hooks/useAuth";
import { Link, useLocation } from "react-router-dom";

const Acknowledge = () => {
  const { id, token } = useParams();
  const [text, setText] = useState("");
  const { mutate: acknowledge } = useMutation(AcknowledgeMail);

  function acknowledgeMail(id, userToken) {
    const payload = {
      status: "Acknowledged",
      id: id,
      token: userToken,
    };
    acknowledge(payload, {
      onSuccess: (data) => {
        setText(data.message);
      },
      onError: (error) => {
        setText("Try again by clicking the Acknowledge button");
      },
    });
  }

  useEffect(() => {
    acknowledgeMail(id, token);
  }, [id, token]);

  return (
    <div className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md={6}>
            <div className="clearfix">
              <h1 className="float-start display-3 me-4">{text}</h1>
              <p
                style={{ fontSize: "20px" }}
                className="text-medium-emphasis float-start"
              >
                {text === "Try again by clicking the Acknowledge button"
                  ? "If you are facing challenges in acknowledging the Mail, Tru again by clicking on the Acknowledge button"
                  : "Kindly take immediate action to stabilize the temperature within the designated range to prevent any further issues or disruptions. Your prompt attention to this matter is greatly appreciated"}
              </p>
            </div>
            <Link to="/login">
              <CButton
                style={{ width: "100px", height: "50px" }}
                color="primary"
                className="float-center"
              >
                Login
              </CButton>
            </Link>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  );
};

export default Acknowledge;
